import Vue from 'vue'
import VueRouter from 'vue-router'
import Discover from '@/components/Discover.vue'
import Friends from '@/components/Friends.vue'
import My from '@/components/My.vue'
import Foreign from '@/components/Foreign.vue'
import Native from '@/components/Native.vue'
import Store from '@/components/Store.vue'
import Commondity from '@/components/Commondity.vue'
import Big from '@/components/Big.vue'
import MidOne from '@/components/MidOne.vue'
import LittleOne from '@/components/LittleOne'

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { path: '/discover', redirect: '/discover/native' },
    { path: '/store', redirect: '/store/1' },
    {
      path: '/discover',
      component: Discover,
      children: [
        { path: 'native', component: Native },
        { path: 'foreign', component: Foreign }
      ]
    },
    { path: '/friends', component: Friends },
    { path: '/my', component: My },
    { path: '/', component: Big },
    { path: '/:classId', name: 'detail', component: MidOne, props: true }, // The mid class router
    { path: '/:classId2', name: 'midToLittle', component: LittleOne, props: true }, // The little class router
    {
      path: '/store',
      component: Store,
      children: [
        { path: ':id', component: Commondity, props: true }
      ]
    }
  ]
})

export default router
